import Footer from "@/components/Footer.vue"
import axios from "axios";

export default {
  name: "Profile",
  data() {
    return {
      user: "",
      id: "",
      letterCard: "",
      showContent: "", 
      cards: []
    };
  },
  created() {
    const userId = this.$route.params.userId;
     axios.get(`https://backend-turtle-f3fpv7l8v-tiagobonito44-hotmailcom.vercel.app/takeUser/${userId}`)
    .then(response => {
      this.user = response.data;
      this.id = this.user._id 
      console.log(this.user)
    })
    .catch(error => {
      console.error(error);
    });
  },


  methods: {

  
    async sendCard() {
      if(this.letterCard.length >= 40) {
        return alert("A mensagem √© grande demais!")
      
      }
      if(this.letterCard != "") {
      const card = {
        userId: this.id,
        content: this.letterCard
      }

      await axios.post(`https://backend-turtle-f3fpv7l8v-tiagobonito44-hotmailcom.vercel.app/newCard/${this.id}`, card)
      .then((res) => {
        console.log(res)
      })
      .catch((err) => {
        console.log(err)
      })
      this.letterCard = ""
    } else {
      return 
    }
  },

    
    async toggleShowCards() {
      this.showContent = !this.showContent;
      
      if (this.showContent) {
        
        try {
          await axios.get(`https://backend-turtle-f3fpv7l8v-tiagobonito44-hotmailcom.vercel.app/showCards/${this.id}`)
        .then((res) => {
          console.log(res.data)
          this.cards = res.data.reverse()
          this.cards = res.data

        })
        .catch((err) => {
          console.log(err)
        })
        } catch (e) {
          console.log(e)
        }
      }
    }
  },

  components: {
    Footer
  }

  }
